import React, { Component } from 'react'
import Gasto from './Gasto';
import Presupuesto from './Presupuesto';
import Resto from './Resto';

export default class ListaGastos extends Component {
    render() {
        let {gastos}= this.props;        
        return (
            <React.Fragment>
                <Presupuesto presupuesto={this.props.presupuesto}></Presupuesto>
                <Resto resto={this.props.resto} presupuesto={this.props.presupuesto}></Resto>
                {/* lo que te queda */}
                <div className="list-group mt-2">
                    <h4>Lista de Gastos</h4>
                    <br/>
                    {gastos.map(gasto=><Gasto key={gasto.id} gasto={gasto}></Gasto>)}                         
                </div>            
            </React.Fragment>
        )
    }
}
