import React, { Component } from 'react'
import uuid from 'uuid';
import PropTypes from 'prop-types';
export default class Formulario extends Component {

    descripcionRef;
    montoRef;
    fechaRe;

    constructor() {
        super();
        this.descripcionRef = React.createRef();
        this.montoRef = React.createRef();
        this.fechaRe = React.createRef();
        this.state = {
            error: false,
        }
    }

    enviarGasto = (e) => {
        e.preventDefault();

        if (this.descripcionRef.current.value == "" ||
            this.montoRef.current.value == "" ||
            this.fechaRe.current.value == "") {
            this.setState({
                error: true
            });
            return;
        }

        let objGasto = {
            id: uuid(),
            descripcion: this.descripcionRef.current.value,
            monto: this.montoRef.current.value,
            fecha: this.fechaRe.current.value
        }
        e.currentTarget.reset();

        this.props.agregarGasto(objGasto);
        this.setState({
            error: false
        })
    }



    render() {
        return (
            <div>
                <form onSubmit={this.enviarGasto}>
                    <fieldset>
                        <legend>Gastos y presupuestos</legend>
                        <div className="form-group row">
                            <label htmlFor="staticEmail" className="col-sm-2 col-form-label">Email</label>
                            <div className="col-sm-10">
                                <input type="text" readOnly className="form-control-plaintext" id="staticEmail" defaultValue="email@example.com" />
                            </div>
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleInputEmail1">Descripcion del gasto</label>
                            <input type="text" className="form-control" id="exampleInputEmail1" placeholder="Ejm: comida" ref={this.descripcionRef} />
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleInputPassword1">Monto del gasto</label>
                            <input type="number" className="form-control" id="exampleInputPassword1" placeholder="Ejm: 100.00" ref={this.montoRef} />
                        </div>
                        <div className="form-group">
                            <label htmlFor="fechaDelGasto">Fecha del gasto</label>
                            <input type="date" className="form-control" id="fechaDelGasto" ref={this.fechaRe} />
                        </div>
                        <button type="submit" className="btn btn-primary">Crear Gasto</button>
                    </fieldset>
                    {
                        this.state.error ?
                            <div className="alert alert-danger"> Todos los campos son obligatorios</div> : null
                    }
                </form>
            </div>
        )
    }
}

Formulario.propTypes = {
    agregarGasto: PropTypes.func.isRequired,
}